import axios from '@nextcloud/axios';
import { generateRemoteUrl, generateUrl } from '@nextcloud/router';
import { getCurrentUser } from '@nextcloud/auth';
import PropertyList from './PropertyList';
import EmptyPropertiesPlaceholder from '../../components/emptypropertiesplaceholder/EmptyPropertiesPlaceholder';
import Tab from '@nextcloud/vue/dist/Components/AppSidebarTab';
import { isEmptyObject, xmlToTagList } from './utils';
export default {
  name: 'Sidebar',
  components: {
    EmptyPropertiesPlaceholder,
    PropertyList,
    Tab
  },
  props: {
    fileInfo: {
      type: Object,
      default: () => {},
      required: true
    }
  },

  data() {
    return {
      name: t('customproperties', 'Properties'),
      icon: 'icon-info',
      loading: true,
      properties: {
        knownProperties: [],
        otherProperties: []
      }
    };
  },

  computed: {
    id() {
      return 'customproperties';
    },

    activeTab() {
      return this.$parent.activeTab;
    }

  },
  watch: {
    fileInfo(newFile, oldFile) {
      if (newFile.id !== oldFile.id) {
        this.update();
      }
    }

  },

  async beforeMount() {
    await this.update();
  },

  methods: {
    async update() {
      this.properties.knownProperties = [];
      this.properties.otherProperties = [];

      if (!isEmptyObject(this.fileInfo)) {
        this.loading = true;
        const properties = await this.retrieveProps();
        const customProperties = await this.retrieveCustomProperties();
        const customPropertyNames = customProperties.map(cp => "".concat(cp.prefix, ":").concat(cp.propertyname));
        this.properties.knownProperties = customProperties.map(cp => {
          const property = properties.find(p => "".concat(cp.prefix, ":").concat(cp.propertyname) === p.propertyname);
          return { ...property,
            ...cp
          };
        });
        this.properties.otherProperties = properties.filter(property => {
          return !customPropertyNames.includes(property.propertyname);
        }).map(property => {
          return {
            propertylabel: property.propertyname,
            ...property
          };
        });
        this.loading = false;
      }
    },

    async retrieveCustomProperties() {
      try {
        const customPropertiesUrl = generateUrl('/apps/customproperties/customproperties');
        const customPropertiesResponse = await axios.get(customPropertiesUrl);
        return customPropertiesResponse.data;
      } catch (e) {
        console.error(e);
        return [];
      }
    },

    async retrieveProps() {
      try {
        const uid = getCurrentUser().uid;
        const path = "/files/".concat(uid, "/").concat(this.fileInfo.path, "/").concat(this.fileInfo.name).replace(/\/+/ig, '/');
        const url = generateRemoteUrl('dav') + path;
        const result = await axios.request({
          method: 'PROPFIND',
          url,
          data: '<d:propfind xmlns:d="DAV:"></d:propfind>'
        });
        return xmlToTagList(result.data);
      } catch (e) {
        console.error(e);
        return [];
      }
    },

    async updateProperty(property) {
      const uid = getCurrentUser().uid;
      const path = "/files/".concat(uid, "/").concat(this.fileInfo.path, "/").concat(this.fileInfo.name).replace(/\/+/ig, '/');
      const url = generateRemoteUrl('dav') + path;
      const propTag = "".concat(property.prefix, ":").concat(property.propertyname);

      try {
        await axios.request({
          method: 'PROPPATCH',
          url,
          data: "\n            <d:propertyupdate xmlns:d=\"DAV:\" xmlns:oc=\"http://owncloud.org/ns\">\n             <d:set>\n               <d:prop>\n                <".concat(propTag, ">").concat(property.propertyvalue, "</").concat(propTag, ">\n               </d:prop>\n             </d:set>\n            </d:propertyupdate>")
        });
      } catch (e) {
        console.error(e);
      }
    }

  }
};