export default {
  name: 'PropertyLinkInput',
  model: {
    prop: 'property'
  },
  props: {
    property: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      property_: this.property
    };
  },

  methods: {
    blur() {
      this.$emit('blur', this.property_);
    }

  }
};